import React from 'react'
import ListView, { SORT_BY } from './ListView'
import LoadingSpinner from './LoadingSpinner'

export const MAIN_DISPLAY_INDEX = {
    WELCOME: 0,
    LIST_IMPORTANT: 1,
    LIST_URGENT: 2
}

export default function MainDisplay({page}) {
    console.log('render MainDisplay')
    let content = <LoadingSpinner/>
    let INDEX = MAIN_DISPLAY_INDEX
    if (page === INDEX.WELCOME) {
        content = 'Welcome!'
    }
    else if (page === INDEX.LIST_IMPORTANT) {
        content = <ListView sort_fn={SORT_BY.PRIORITY}/>
    }
    else if (page === INDEX.LIST_URGENT) {
        content = <ListView sort_fn={SORT_BY.DUE}/>
    }
    return (
        <main className="overflow-x-hidden overflow-y-auto flex-grow-1">
            {content}
        </main>
    )
}

