import React, { useContext, useState, useEffect } from 'react'
import { xcContext } from './xcContext'
import ItemInList from './ItemInList'
import LoadingSpinner from './LoadingSpinner'

export default function ListView({sort_fn}) {

    console.log('render ListView')
    const xc = useContext(xcContext)
    const [content, setContent] = useState(<LoadingSpinner/>)

    useEffect(
        () => {
            const format = (task_map) => {
                // Make list of tasks from map of tasks
                let list = []
                task_map.forEach((value, key)=>{
                    list.push(value)
                })
        
                // order list of tasks
                let sorted_list = sort_fn(list)
        
                // format into jsx
                let formatted_list = []
                sorted_list.forEach((task)=> {
                    formatted_list.push(
                        <ItemInList t={task} key={task.id}/>
                    )
                })
                return (
                    <article className='d-flex pt-0 px-3 flex-column'>
                        {formatted_list}
                    </article>
                )
            }
            console.log('calling get_all_tasks()')
            xc.get_all_tasks()
            .then(
                (result) => {
                    setContent(format(result))
                }
            )
        },
        [xc, sort_fn, setContent]
    )
    return content
}

export const SORT_BY = {
    DUE(list) {
        list.sort((a,b)=>a.dueDate - b.dueDate)
        return list
    },
    PRIORITY(list) {
        list.sort((a,b)=>-(a.priority - b.priority))
        return list
    }
}